'use client'

import { useState, useMemo } from 'react'
import { motion, AnimatePresence, LayoutGroup } from 'framer-motion'
import Image from 'next/image'

// --- DADOS DAS IMAGENS ---
// Adicione ou remova imagens aqui. A categoria ('eventos' ou 'brindes') é usada para o filtro.
const allPortfolioImages = [
  // Eventos
  { id: 'evt-1', src: '/images/1.jpg', category: 'eventos', alt: 'Foto de Evento 1' },
  { id: 'evt-2', src: '/images/2.jpg', category: 'eventos', alt: 'Foto de Evento 2' },
  { id: 'evt-3', src: '/images/3.jpg', category: 'eventos', alt: 'Foto de Evento 3' },
  { id: 'evt-4', src: '/images/4.jpg', category: 'eventos', alt: 'Foto de Evento 4' },
  { id: 'evt-5', src: '/images/5.jpg', category: 'eventos', alt: 'Foto de Evento 5' },
  { id: 'evt-6', src: '/images/6.jpg', category: 'eventos', alt: 'Foto de Evento 6' },
  { id: 'evt-7', src: '/images/7.jpg', category: 'eventos', alt: 'Foto de Evento 7' },
  { id: 'evt-8', src: '/images/8.jpg', category: 'eventos', alt: 'Foto de Evento 8' },
  { id: 'evt-9', src: '/images/9.jpg', category: 'eventos', alt: 'Foto de Evento 9' },
  { id: 'evt-10', src: '/images/10.jpg', category: 'eventos', alt: 'Foto de Evento 10' },
  { id: 'evt-11', src: '/images/11.jpg', category: 'eventos', alt: 'Foto de Evento 11' },
  { id: 'evt-12', src: '/images/12.jpg', category: 'eventos', alt: 'Foto de Evento 12' },
  // Brindes
  { id: 'brd-1', src: '/images/brindes/Camiseta Preta.png', category: 'brindes', alt: 'Camiseta Personalizada' },
  { id: 'brd-2', src: '/images/brindes/brinde-2.jpg', category: 'brindes', alt: 'Copo Personalizado' },
  { id: 'brd-3', src: '/images/brindes/brinde-3.jpg', category: 'brindes', alt: 'Caneta Personalizada' },
  { id: 'brd-4', src: '/images/brindes/brinde-4.jpg', category: 'brindes', alt: 'Chaveiro Personalizado' },
];

type FilterType = 'todos' | 'eventos' | 'brindes';

export default function PortfolioPage() {
  const [activeFilter, setActiveFilter] = useState<FilterType>('todos');
  const [selectedImage, setSelectedImage] = useState<string | null>(null);

  // useMemo para otimizar a filtragem
  const imagesToShow = useMemo(() => {
    if (activeFilter === 'todos') {
      return allPortfolioImages;
    } else {
      return allPortfolioImages.filter(image => image.category === activeFilter);
    }
  }, [activeFilter]);
  
  const getButtonClass = (filter: FilterType) => {
    const baseClass = `
      bg-white/10 backdrop-blur-md border border-white/20 text-ls-branco 
      font-bold py-2 px-6 rounded-full text-base
      transition-all duration-300`;
    
    if (activeFilter === filter) {
      return `${baseClass} bg-ls-laranja/50 border-ls-laranja`;
    }
    return `${baseClass} hover:bg-white/20`;
  };

  return (
    <main className="flex flex-col text-ls-branco min-h-screen">
      <section className="container mx-auto px-4 py-32">
        <motion.h1
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="text-5xl md:text-7xl font-extrabold text-center mb-8"
        >
          Nosso Portfólio
        </motion.h1>
        <motion.p 
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5, delay: 0.2 }}
          className="text-lg text-gray-300 text-center mb-12 max-w-2xl mx-auto"
        >
          Explore alguns dos nossos trabalhos em eventos e os brindes personalizados que criamos.
        </motion.p>

        {/* Botões de Filtro */}
        <motion.div 
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.5, delay: 0.4 }}
          className="flex justify-center items-center gap-4 mb-16"
        >
          <button onClick={() => setActiveFilter('todos')} className={getButtonClass('todos')}>Todos</button>
          <button onClick={() => setActiveFilter('eventos')} className={getButtonClass('eventos')}>Eventos</button>
          <button onClick={() => setActiveFilter('brindes')} className={getButtonClass('brindes')}>Brindes</button>
        </motion.div>

        {/* Grid de Imagens Animado */}
        <LayoutGroup>
          <motion.div layout className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
            <AnimatePresence>
              {imagesToShow.map((image) => (
                <motion.div
                  key={image.id}
                  layout
                  initial={{ opacity: 0, scale: 0.8 }}
                  animate={{ opacity: 1, scale: 1 }}
                  exit={{ opacity: 0, scale: 0.8 }}
                  transition={{ duration: 0.3 }}
                  className="group relative aspect-square rounded-xl overflow-hidden shadow-lg cursor-pointer"
                  onClick={() => setSelectedImage(image.src)}
                >
                  <Image
                    src={image.src}
                    alt={image.alt}
                    fill
                    className="object-cover transition-transform duration-500 group-hover:scale-110"
                    sizes="(max-width: 640px) 100vw, (max-width: 768px) 50vw, (max-width: 1024px) 33vw, 25vw"
                  />
                  {/* Overlay que aparece no hover */}
                  <div className="absolute inset-0 bg-black/50 opacity-0 group-hover:opacity-100 transition-opacity duration-300 flex items-end p-4">
                    <p className="text-white font-semibold text-sm">{image.alt}</p>
                  </div>
                </motion.div>
              ))}
            </AnimatePresence>
          </motion.div>
        </LayoutGroup>

        {/* Lightbox Modal para Imagem Expandida */}
        <AnimatePresence>
          {selectedImage && (
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
              className="fixed inset-0 z-50 flex items-center justify-center bg-black/80 backdrop-blur-sm p-4"
              onClick={() => setSelectedImage(null)}
            >
              <div className="relative w-full h-full max-w-4xl max-h-[90vh]">
                <Image src={selectedImage} alt="Imagem do portfólio expandida" fill className="object-contain" />
              </div>
            </motion.div>
          )}
        </AnimatePresence>
      </section>
    </main>
  );
}